<template>
  <div class="Component-ControlDeFecha">
    <label class="una_etiqueta">{{ internal_label }}</label>
    <table class="tabla_sin_bordes ancho_maximo">
        <tbody class="">
            <tr class="">
                <td class="" style="width: 50%; text-align: center;" v-if="mode === 'full' || mode === 'day'">
                    <input class="un_control" type="date" v-model="internal_date_value" />
                </td>
                <td class="" style="width: 50%; text-align: center;" v-if="mode === 'full' || mode === 'time'">
                    <input class="un_control" type="time" v-model="internal_time_value" />
                </td>
            </tr>
        </tbody>
    </table>
  </div>
</template>
<script>

export default {
    name: "ControlDeFecha",
    props: {
        root: {
            type: Object,
            required: true,
        },
        label: {
            type: String,
            required: false,
        },
        value: {
            type: String,
            required: false,
        },
        placeholder: {
            type: String,
            required: false,
        },
        mode: {
            type: String,
            default: () => "full"
        },
        onChange: {
            type: Function,
            required: false,
            default: () => () => undefined
        },
    },
    data() {
        console.log("Loading data for ControlDeFecha:", this.value);
        return {
            internal_label: this.label,
            internal_date_value: undefined,
            internal_time_value: undefined,
            internal_value: undefined,
            internal_placeholder: this.placeholder,
            internal_on_change: this.onChange,
        };
    },
    methods: {
        
    },
    watch: {
        internal_date_value(v) {
            const date1 = new Date(v);
            const dateStr = this.$utils.getStringFromDate(date1, "dia");
            const dateFinal = dateStr + " " + this.internal_time_value;
            this.internal_value = dateFinal;
        },
        internal_time_value(v) {
            const [ hour, minute ] = v.split(":");
            const date1 = new Date(this.internal_date_value);
            date1.setHours(hour);
            date1.setMinutes(minute);
            const timeStr = this.$utils.getStringFromDate(date1, "minuto").replace(/^[^ ]+ /g, "");
            const dateFinal = this.internal_date_value + " " + timeStr;
            this.internal_value = dateFinal;
        },
        internal_value(v) {
            this.internal_on_change(v, this);
        }
    },
    computed: {},
    beforeCreate() {},
    beforeMount() {},
    beforeUpdate() {},
    beforeDestroy() {},
    created() {},
    mounted() {
        this.internal_date_value = this.value ? this.value.split(" ")[0] : "";
        this.internal_time_value = this.value ? this.value.split(" ")[1] : "00:00";
        Object.assign(this, {
            internal_value: this.value,
        });
    },
    updated() {},
    destroyed() {},
};

</script>
