export default {
    data() {
        return {
            internal_tiempo: undefined,
            internal_tareas_de_tiempo: undefined,
            internal_tareas_de_tiempo_fallidas: undefined,
            internal_tareas_de_tiempo_completadas: undefined,
            internal_tareas_de_tiempo_objetivos_totales: undefined,
            internal_tareas_de_tiempo_objetivos_sumados: [],
            internal_tareas_de_tiempo_objetivos_pendientes: [],
            internal_error: undefined,
        };
    },
    methods: {
        goToAddTarea() {
            this.root.goToPage("PaginaAgendaAniadirTarea", {
                value: {
                    "fecha de inicio": this.$utils.getStringFromDate(this.internal_tiempo)
                }
            });
        },
        goToTarea(tarea) {
            this.root.goToPage("PaginaAgendaEditarTarea", {
                value: tarea
            });
        },
        goToPreviousDay() {
            const dateTmp = new Date(this.internal_tiempo);
            dateTmp.setDate(dateTmp.getDate() - 1);
            this.internal_tiempo = dateTmp;
        },
        goToNextDay() {
            const dateTmp = new Date(this.internal_tiempo);
            dateTmp.setDate(dateTmp.getDate() + 1);
            this.internal_tiempo = dateTmp;
        },
        goToPreviousHour() {
            const dateTmp = new Date(this.internal_tiempo);
            dateTmp.setHours(dateTmp.getHours() - 1);
            this.internal_tiempo = dateTmp;
        },
        goToNextHour() {
            const dateTmp = new Date(this.internal_tiempo);
            dateTmp.setHours(dateTmp.getHours() + 1);
            this.internal_tiempo = dateTmp;
        },
        goToPreviousMonth() {
            const dateTmp = new Date(this.internal_tiempo);
            dateTmp.setMonth(dateTmp.getMonth() - 1);
            this.internal_tiempo = dateTmp;
        },
        goToNextMonth() {
            const dateTmp = new Date(this.internal_tiempo);
            dateTmp.setMonth(dateTmp.getMonth() + 1);
            this.internal_tiempo = dateTmp;
        },
        goToPreviousYear() {
            const dateTmp = new Date(this.internal_tiempo);
            dateTmp.setFullYear(dateTmp.getFullYear() - 1);
            this.internal_tiempo = dateTmp;
        },
        goToNextYear() {
            const dateTmp = new Date(this.internal_tiempo);
            dateTmp.setFullYear(dateTmp.getFullYear() + 1);
            this.internal_tiempo = dateTmp;
        },
        refreshInternalTiempoData(v = this.internal_tiempo) {
            if (!(v instanceof Date)) {
                this.internal_tareas_de_tiempo = undefined;
                return;
            }
            const startDateStr = `${this.$utils.padStart(v.getFullYear(), 4, "0")}-${this.$utils.padStart(v.getMonth() + 1, 2, "0")}-${this.$utils.padStart(v.getDate(), 2, "0")} ${this.$utils.padStart(v.getHours(), 2, "0")}`;
            this.$db
                .select("tarea", {
                    filter: (item) => {
                        return item["fecha de inicio"].startsWith(startDateStr);
                    },
                })
                .then((dataP) => {
                    const data = dataP.sort((a, b) => {
                        return b["fecha de inicio"] < a["fecha de inicio"] ? 1 : a["fecha de inicio"] < b["fecha de inicio"] ? -1 : b.nombre < a.nombre ? 1 : -1;
                    });
                    this.internal_tareas_de_tiempo_fallidas = data.filter((v2) => {
                        return !v2["está terminada"];
                    });
                    this.internal_tareas_de_tiempo_completadas = data.filter((v2) => {
                        return v2["está terminada"];
                    });
                    this.internal_tareas_de_tiempo_objetivos_totales = data.length;
                    this.internal_tareas_de_tiempo_objetivos_sumados = data
                        .filter((v2) => {
                            return v2["está terminada"];
                        })
                        .reduce(this.$utils.reduceObjetivosFromTarea.bind(this), []);
                    this.internal_tareas_de_tiempo_objetivos_pendientes = data
                        .filter((v2) => {
                            return !v2["está terminada"];
                        })
                        .reduce(this.$utils.reduceObjetivosFromTarea.bind(this), []);
                    this.internal_tareas_de_tiempo_objetivos_totales = this.internal_tareas_de_tiempo_objetivos_sumados.length + this.internal_tareas_de_tiempo_objetivos_pendientes.length;
                    this.internal_tareas_de_tiempo = data;
                    return data;
                })
                .catch((error) => {
                    this.internal_error = error;
                    throw error;
                });
        },
    },
    watch: {
        internal_tiempo(v) {
            this.refreshInternalTiempoData(v);
        },
    },
    directProperties: {
        computed: {},
        beforeCreate() { },
        beforeMount() { },
        beforeUpdate() { },
        beforeDestroy() { },
        created() { },
        mounted() {
            this.internal_tiempo = new Date();
        },
        updated() { },
        destroyed() { },
    }
};