
<template>
  <div class="Component-PaginaAgendaVerCalendario">
    <h3 class="texto_importante_1"><span class="boton_atras" v-on:click="root.goBack"> « </span>Calendario</h3>
    <div class="un_espaciado" v-if="!internal_selected_date || !internal_tasks_per_day">Cargando calendario... Por favor, espere...</div>
    <div class="un_espaciado" v-else>
      <div class="un_texto_pequenio">
        <div class="un_espaciado un_texto_importante" style="text-align: center; cursor: pointer" v-on:click="() => (internal_selected_date = internal_today_date)">Hoy es: {{ $utils.getPoliteStringFromDate(internal_today_date) }}</div>
      </div>
      <table class="una_tabla tabla_sin_bordes la_tabla_de_calendario">
        <tbody>
          <tr>
            <td colspan="100">
              <table class="una_tabla tabla_sin_bordes">
                <tbody>
                  <tr>
                    <td class="ancho_minimo un_espaciado_arriba">
                      <button class="un_control una_anchura_de_20_px" type="button" v-on:click="() => goToPreviousMonth()">«</button>
                    </td>
                    <td class="ancho_maximo texto_centrado un_texto_pequenio">
                      {{ internal_month_name }}
                    </td>
                    <td class="ancho_minimo un_espaciado_arriba">
                      <button class="un_control una_anchura_de_20_px" type="button" v-on:click="() => goToNextMonth()">»</button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </td>
          </tr>
          <tr>
            <td colspan="100">
              <table class="una_tabla tabla_sin_bordes">
                <tbody>
                  <tr>
                    <td class="ancho_minimo un_espaciado_arriba">
                      <button class="un_control una_anchura_de_20_px" type="button" v-on:click="() => goToPreviousYear()">«</button>
                    </td>
                    <td class="ancho_maximo texto_centrado un_texto_pequenio">
                      {{ internal_year_name }}
                    </td>
                    <td class="ancho_minimo un_espaciado_arriba">
                      <button class="un_control una_anchura_de_20_px" type="button" v-on:click="() => goToNextYear()">»</button>
                    </td>
                  </tr>
                </tbody>
              </table>
            </td>
          </tr>
        </tbody>
        <tbody>
          <tr class="la_fila_de_dias_de_la_semana">
            <td class="una_celda_de_dia_de_calendario"><div class="el_contenido_de_cabecera_de_celda un_texto_pequenio">LUN</div></td>
            <td class="una_celda_de_dia_de_calendario"><div class="el_contenido_de_cabecera_de_celda un_texto_pequenio">MAR</div></td>
            <td class="una_celda_de_dia_de_calendario"><div class="el_contenido_de_cabecera_de_celda un_texto_pequenio">MIE</div></td>
            <td class="una_celda_de_dia_de_calendario"><div class="el_contenido_de_cabecera_de_celda un_texto_pequenio">JUE</div></td>
            <td class="una_celda_de_dia_de_calendario"><div class="el_contenido_de_cabecera_de_celda un_texto_pequenio">VIE</div></td>
            <td class="una_celda_de_dia_de_calendario"><div class="el_contenido_de_cabecera_de_celda un_texto_pequenio">SAB</div></td>
            <td class="una_celda_de_dia_de_calendario"><div class="el_contenido_de_cabecera_de_celda un_texto_pequenio">DOM</div></td>
          </tr>
          <tr v-for="(row, rowIndex) in internal_month_rows" v-bind:key="'month-row-' + rowIndex + '-row'">
            <td
              class="una_celda_de_dia_de_calendario"
              :class="{
                [cell.day ? 'el_contenido_de_cuerpo_de_celda' : 'el_contenido_de_cuerpo_vacio_de_celda']: true,
                el_dia_seleccionado: cell.year === internal_today_date.getFullYear() && cell.month === internal_today_date.getMonth() && cell.day === internal_today_date.getDate(),
                un_dia_pasado:
                  false ||
                  cell.year < internal_today_date.getFullYear() ||
                  (cell.year === internal_today_date.getFullYear() && cell.month < internal_today_date.getMonth()) ||
                  (cell.year === internal_today_date.getFullYear() && cell.month === internal_today_date.getMonth() && cell.day < internal_today_date.getDate()),
              }"
              v-on:click="() => cell.day && goToDay(cell)"
              v-for="(cell, cellIndex) in row"
              v-bind:key="'month-row-' + rowIndex + '-cell-' + cellIndex"
            >
              <div class="el_contenedor_de_las_concurrencias_del_dia" v-if="cell.day">
                <div class="un_dia_del_calendario">{{ cell.day }}</div>
                <div class="las_concurrencias_del_dia" v-if="cell.day in internal_tasks_per_day">
                  <span class="el_checker_de_tareas"> ✓ </span>
                  <span class="el_numero_de_tareas">{{ getTareasActivasByDay(cell.day).length }}/{{ internal_tasks_per_day[cell.day].length }}</span>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
        <tbody>
          <tr>
            <td class="ancho_maximo" colspan="100">
              <div class="el_panel_avanzado_del_calendario un_espaciado_sin_lados">
                <ControlDeSelector ref="advancedPanelSelector" :root="root" label="Comandos del calendario:" :options="['', 'Copiar día']" :value="''" :on-change="(v) => (internal_selected_advanced_panel = v)" />
              </div>
              <div class="el_panel_avanzado_del_calendario un_espaciado" v-if="internal_selected_advanced_panel === 'Copiar día'">
                <div class="un_espaciado_sin_lados">
                  <ControlDeFecha :root="root" label="Fecha de origen:" :on-change="(v) => (internal_copy_date_source = v)" mode="day" />
                </div>
                <div class="un_espaciado_sin_lados">
                  <ControlDeFecha :root="root" label="Fecha de destino:" :on-change="(v) => (internal_copy_date_destination = v)" mode="day" />
                </div>
                <div class="un_espaciado_sin_lados">
                  <button class="un_control" v-on:click="() => copyDayToDay()">Copiar</button>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <div style="min-height: 150px"></div>
    </div>
  </div>
</template>
<script>
export default {
  name: "PaginaAgendaVerCalendario",
  props: {
    root: {
      type: Object,
      required: true,
    },
  },
  data() {
    const today = new Date();
    return {
      internal_selected_date: undefined,
      internal_today_date: today,
      internal_month_rows: [],
      internal_month_id: undefined,
      internal_month_name: undefined,
      internal_tasks_per_day: {},
      internal_year_name: undefined,
      internal_selected_advanced_panel: false,
      internal_copy_date_source: undefined,
      internal_copy_date_destination: undefined,
    };
  },
  methods: {
    goToPreviousYear() {
      const otherDate = new Date(this.internal_selected_date);
      otherDate.setYear(otherDate.getFullYear() - 1);
      this.internal_selected_date = otherDate;
    },
    goToNextYear() {
      const otherDate = new Date(this.internal_selected_date);
      otherDate.setYear(otherDate.getFullYear() + 1);
      this.internal_selected_date = otherDate;
    },
    goToPreviousMonth() {
      const otherDate = new Date(this.internal_selected_date);
      otherDate.setMonth(otherDate.getMonth() - 1);
      this.internal_selected_date = otherDate;
    },
    goToNextMonth() {
      const otherDate = new Date(this.internal_selected_date);
      otherDate.setMonth(otherDate.getMonth() + 1);
      this.internal_selected_date = otherDate;
    },
    goToDay(cell) {
      const targetDate = this.$utils.getDateFromString(`${cell.year}-${cell.month + 1}-${cell.day} 00:00`);
      this.root.goToPage("PaginaAgendaVerAgenda", { targetDate });
    },
    getTareasActivasByDay(day) {
      return [].concat(this.internal_tasks_per_day[day]).filter((v) => v["está terminada"]);
    },
    async refreshTareas(v) {
      try {
        const allTareas = await this.$db.select("tarea", { filter: (v) => this.$noop(v) || true });
        this.internal_tasks_per_day = allTareas.reduce((output, item) => {
          const itemDateCrude = item["fecha de inicio"];
          if (typeof itemDateCrude === "undefined") {
            return output;
          }
          const itemDate = this.$utils.getDateFromString(itemDateCrude);
          const isSameMonth = itemDate.getMonth() === v.getMonth() && itemDate.getFullYear() === v.getFullYear();
          if (isSameMonth) {
            const itemDay = "" + itemDate.getDate();
            if (!(itemDay in output)) {
              output[itemDay] = [];
            }
            output[itemDay].push(item);
          }
          return output;
        }, {});
        window.cals = this;
        this.$forceUpdate(true);
      } catch (error) {
        console.error(error);
      }
    },
    copyDayToDay() {
      const dateSrc2 = this.$utils.getDateFromString(this.internal_copy_date_source);
      dateSrc2.setFullYear(dateSrc2.getFullYear());
      dateSrc2.setMonth(dateSrc2.getMonth());
      dateSrc2.setDate(dateSrc2.getDate());
      const dateDst2 = this.$utils.getDateFromString(this.internal_copy_date_destination);
      dateDst2.setFullYear(dateDst2.getFullYear());
      dateDst2.setMonth(dateDst2.getMonth());
      dateDst2.setDate(dateDst2.getDate());
      const dateSrcStr = this.$utils.getStringFromDate(dateSrc2, "dia");
      const dateDstStr = this.$utils.getStringFromDate(dateDst2, "dia");
      this.$db.select("tarea", {
        filter: (tarea) => {
          if(tarea["fecha de inicio"].startsWith(dateSrcStr)) {
            return true;
          }
          return false;
        }
      }).then(tareasSrc => {
        const allPromises = [];
        for(let indexTarea = 0; indexTarea < tareasSrc.length; indexTarea++) {
          const tareaSrc = tareasSrc[indexTarea];
          const tareaSrc2 = Object.assign({}, tareaSrc, {
            "nombre": this.$utils.resetTareaId(tareaSrc.nombre),
            "fecha de inicio": tareaSrc["fecha de inicio"].replace(/^[0-9][0-9][0-9][0-9]-[0-9][0-9]-[0-9][0-9]/g, () => {
              return dateDstStr;
            })
          });
          delete tareaSrc2.id;
          console.log("  - Copiando tarea:", tareaSrc2.nombre);
          allPromises.push(this.$db.insert("tarea", tareaSrc2));
        }
        return new Promise((ok) => {
          Promise.all(allPromises).then(() => ok(tareasSrc));
        });
      }).then(tareasSrc => {
        this.internal_selected_advanced_panel = "";
        this.internal_copy_date_source = undefined;
        this.internal_copy_date_destination = undefined;
        this.$refs.advancedPanelSelector.internal_value = "";
        this.internal_selected_date = new Date(this.internal_selected_date);
        return tareasSrc;
      });
    }
  },
  watch: {
    internal_selected_date(v) {
      if (!(v instanceof Date)) {
        this.internal_month_rows = [];
      }
      // @TODO...
      const today = new Date();
      this.$noop(today);
      const rows = [];
      const currentMonth = v.getMonth();
      this.internal_month_id = currentMonth;
      this.internal_month_name = this.$utils.ALL_MONTHS[currentMonth];
      this.internal_year_name = v.getFullYear();
      const tempDate = new Date(v);
      tempDate.setDate(1);
      const firstWeekdayTmp = tempDate.getDay();
      // Primer día de la semana en el mes:
      const firstWeekday = firstWeekdayTmp === 0 ? 6 : firstWeekdayTmp - 1;
      // Total de días en el mes:
      tempDate.setMonth(tempDate.getMonth() + 1);
      tempDate.setDate(0);
      const lastMonthDate = tempDate.getDate();
      const totalCells = firstWeekday + lastMonthDate;
      let usedCells = 0;
      let currentRow = 0;
      let currentDay = 1;
      rows.push([]);
      while (usedCells < totalCells) {
        if (usedCells !== 0 && usedCells % 7 === 0) {
          currentRow++;
          rows[currentRow] = [];
        }
        if (usedCells < firstWeekday) {
          rows[currentRow].push({ day: false });
        } else {
          rows[currentRow].push({
            day: currentDay++,
            month: v.getMonth(),
            year: v.getFullYear(),
          });
        }
        usedCells++;
      }
      this.internal_month_rows = rows;
      return this.refreshTareas(v);
    },
  },
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  mounted() {
    this.internal_selected_date = new Date();
  },
  updated() {},
  destroyed() {},
};
</script>
<style>
.Component-PaginaAgendaVerCalendario {
}
</style>
